.diet__wrapper {
  min-width: 280px;
  padding: 0 20px;
  margin-top: 22px;

  @media (min-width: $tablet) {
    padding: 0 30px;
    margin: 0;
  }

  @media (min-width: $desktop) {
    padding: 0 110px;
    width: 1220px;
    margin: 0 auto;
  }
}

.diet__list {
  list-style: none;
  padding: 0;
  margin: 0;

  @media (min-width: $desktop) {
    margin: 0;
    padding: 0;
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 80px;
    width: auto;
  }
}

.diet__item {
  position: relative;
  background-color: $basic-background;
  margin-bottom: 20px;
  padding: 0 20px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;

  &:nth-last-child(1) {
    margin-bottom: 0;
  }

  @media (min-width: $tablet) {
    padding: 0;
    padding-top: 77px;
    padding-bottom: 56px;
    margin: 0;
    margin-bottom: 30px;
    flex-direction: column;
    align-items: flex-start;

    &:nth-last-child(1) {
      margin-bottom: 0;
    }
  }

  @media (min-width: $desktop) {
    margin: 0;
    padding: 0 52px;
    padding-top: 45px;
    padding-bottom: 55px;
    flex-direction: row;
    min-height: 274px;
  }
}

.diet__marker {
  position: relative;
  background: url("../img/diet-marker.svg");
  width: 50px;
  height: 50px;
  background-repeat: no-repeat;

  &::after {
    position: absolute;
    content: "";
    transform: translate(-50%, -50%);
    background-repeat: no-repeat;
  }

  @media (min-width: $tablet) {
    position: absolute;
    right: 45px;
    top: 92px;
    order: 3;
    width: 200px;
    height: 200px;
  }

  @media (min-width: $desktop) {
    position: relative;
    width: 100px;
    height: 100px;
    order: 1;
    transform: none;
    top: auto;
    right: auto;
  }
}

.diet__marker--slim::after {
  background: url("../img/diet-slim.svg");
  width: 36px;
  height: 50px;
  top: 45%;
  left: 50%;
  background-repeat: no-repeat;

  @media (min-width: $tablet) {
    width: 140px;
    height: 192px;
  }

  @media (min-width: $desktop) {
    width: 70px;
    height: 97px;
  }
}

.diet__marker--pro::after {
  background: url("../img/diet-pro.svg");
  width: 67px;
  height: 28px;
  top: 50%;
  left: 50%;
  background-repeat: no-repeat;

  @media (min-width: $tablet) {
    width: 268px;
    height: 112px;
  }

  @media (min-width: $desktop) {
    width: 134px;
    height: 56px;
  }
}

.diet__title {
  text-transform: uppercase;
  padding: 30px 0;
  margin: 0;
  margin-left: 20px;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 37px;
  max-width: 75%;

  @media (min-width: $tablet) {
    padding: 0;
    order: 1;
    margin: 0;
    padding-left: 53px;
    padding-bottom: 25px;
    font-size: 36px;
    line-height: 36px;
    width: calc(100% - 282px);
  }

  @media (min-width: $desktop) {
    padding: 0;
    margin: 0;
    margin-left: 62px;
    order: 2;
    width: auto;
    align-self: center;
  }
}

.diet__info {
  padding-bottom: 23px;
  margin: 0;
  font-family: "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
  color: $text-light;
  border-bottom: 1px solid $border-block;
  width: 100%;

  @media (min-width: $tablet) {
    width: calc(100% - 308px);
    padding: 0;
    padding-left: 52px;
    padding-bottom: 52px;
    margin: 0;
    order: 2;
    border: none;
    font-size: 16px;
    line-height: 24px;
    min-height: 96px;

    &--pro {
      width: calc(100% - 348px);
    }
  }

  @media (min-width: $desktop) {
    padding: 0;
    padding-top: 41px;
    margin: 0;
    order: 3;
    width: 100%;
  }
}

.diet__link {
  position: relative;
  display: inline-block;
  text-decoration: none;
  text-transform: uppercase;
  color: $basic-black;
  font-size: 16px;
  line-height: 16px;
  padding: 19px 0;
  padding-right: 49px;

  &:active {
    opacity: 0.3;
  }

  @media (min-width: $tablet) {
    padding: 0;
    padding-left: 53px;
    margin: 0;
    order: 4;
    font-size: 20px;
    line-height: 30px;
    width: auto;
    padding-right: 58px;
  }

  @media (min-width: $desktop) {
    position: relative;
    padding: 0;
    margin: 0;
    order: 4;
    padding-right: 58px;
  }
}

.diet__link::after {
  position: absolute;
  content: "";
  right: 0;
  top: 21px;
  background-image: url("../img/icon_arrow.svg");
  background-repeat: no-repeat;
  width: 32px;
  height: 10px;

  @media (min-width: $tablet) {
    top: 10px;
  }
}

.diet__link:hover::after {
  background-image: url("../img/icon_arrow_hover.svg");
}
